html,body{
  margin: 0;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  font-size: 16px;
  overflow-y: auto!important;
}
h1,h2,h3,h4,h6{
  margin: 0;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  background-color: #24093B;
  padding: 0!important;
  color: white;
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 3px 0 5px -3px black;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background: #D92F85;
  border-radius: 10px;
}
.MuiToolbar-regular {
  color: #fff;
}
.MuiButton-containedPrimary,
.MuiButton-containedPrimary:hover,
.MuiListItem-button:hover,
.MuiButtonBase-root:hover {
  color: #fff !important;
}
.wallet-button {
  color: white;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  font-size: 1.2rem !important;
  border-radius: 15px !important;
  padding-left: 30px !important;
  padding-right: 30px !important;
  text-align: left !important;
  justify-content: flex-start !important;
  text-transform: none !important;
}
.wallet-button:hover {
  color: #D92F85 !important;
}
.navLink, .formControl a {
  color: white !important;
}
.navLink:hover {
  color: #D92F85 !important;
}
.tokenLink {
  color: #D92F85 !important;
}
.tokenLink:hover {
  color: #D92F85 !important;
}

.shinyButtonDisabled {
  border-radius: 15px;
  color: rgb(214, 52, 52) !important;
  box-shadow: 0px 0px 5px 0px rgb(228, 93, 93);
}
.shinyButton:hover {
  background-color: #D92F85 !important;
}
.shinyButtonHomeDisabled {
  border-radius: 15px !important;
  color: rgb(214, 52, 52) !important;
  box-shadow: 0px 0px 5px 0px rgb(228, 93, 93);
}
.shinyButtonHome:hover {
  background-color: #D92F85 !important;
  color: #f7f3ef !important;
}
button.shinyButtonSecondary, a.shinyButtonSecondary {
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 15px;
  font-size: 1rem;
  padding-left: 5px;
  padding-left: 30px;
  padding-right: 30px;
  background-color: #D92F85;
  color: #f7eff2;
  box-shadow: 0 0 0 0 white;
  transition: box-shadow .3s;
  margin-right: .5rem;
  margin-bottom: .5rem;
}
.shinyButtonSecondary.bgYellow {
  background-color: #e9c910;
}
.shinyButtonSecondaryFarm {
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 15px !important;
  font-size: 1rem !important;
  padding-left: 5px;
  padding-left: 30px !important;
  padding-right: 30px !important;
  background-color: #8247e5 !important;
  color: #f7eff2 !important;
  opacity: 1;
  transition: opacity .3s ease-out;
}
.shinyButtonSecondaryFarm:hover {
  opacity: .5;
}
.shinyButtonDisabledSecondary {
  background-color: red;
  border-radius: 15px;
  color: red !important;
  box-shadow: 0px 0px 5px 0px red;
}
button.shinyButtonSecondary:hover, a.shinyButtonSecondary:hover {
  background-color: #D92F85;
  color: #ffffff;
  box-shadow: 0px 0px 5px 0px white;
}
a.shinyButtonSecondary:hover {
  background-color: #e9c910;
  color: black!important;
}
a.shinyButtonUI {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  font-size: 1rem;

}
a.shinyButtonUI:hover {
  background-color: #D92F85;
  color: #f7f3ef;
}
.shinyButtonUI8 {
  background-color: #e9c910;
  color: #ff2508 !important;
}
.navTokenPrice {
  padding-top: 5px;
  padding-right: 15px;
  color: #fff;
}
.navTokenIcon {
  height: 30px !important;
  width: 30px !important;
  padding-right: 12px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  overflow: hidden;
}
.ant {
  background-image: url('/PDDOLLAR.png');
}
.bshare {
  background-image: url('/PDBOND.png');
}
.btc {
  background-image: url('/PDSHARE.png');
}
header.MuiPaper-root{
  background-color: #4F169C;
}
.headerCustom {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.headerCustom img{
  height: 8vh;
  padding-top: .5rem;
  padding-bottom: .5rem;
}
.mainMenu{
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  width: 50vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
button.buttonDropDown{
  font-size: initial;
  padding-left: 16px;
}
.buttonDropDown .MuiButton-label{
  font-size: 1.2em;
}
.buttonDropDown .MuiButton-label:hover{
  color: #D92F85;
}
.menuDropDown .MuiPaper-root{
  margin-top: 3rem;
  margin-left: .5rem;
}
.mainMenu .formControl{
    min-width:100px;
    margin-top: -16px
}
.mainMenu .formControl label{
  font-size: 1.2em;
  text-transform: uppercase;
  margin-left:1.5rem
}
.mainMenu .formControl svg{
  fill: white;
}
.formControl .MuiInput-underline:before{
  border-bottom: 0;
}
.formControl .MuiInput-underline:hover:not(.Mui-disabled):before{
  border-bottom: 0;
}
.formControl .MuiInputBase-input{
  padding-top: 1rem;
  padding-bottom: 0;
}
.formControl .MuiSelect-icon{
  top: 0;
}
.mainMenu .formControl:hover label{
  color: #D92F85;
}
a.linkDropMenu{
  color: white;
  text-decoration: none;
}
.menuDropDown ul li:hover{
  background-color: #371255;
}
.menuDropDown ul li:hover a.linkDropMenu{
  color: #D92F85;
}
header .buttonDropDown.MuiButton-root:hover{
  background-color: transparent!important;
}
span.MuiTypography-body1{
  font-size: 1.2em;
  text-transform: uppercase;
}
a.MuiListItem-button:hover{
  background-color: #371255;
}
a.MuiListItem-button:hover span{
  color: #D92F85;
}
.tokenCard.deposit img, .claimReward img{
  width: auto;
  
  margin-bottom: 0.5rem;
  max-height: 75px;
}
.modalPop{
  background-color: #4f179b;
  padding: 2rem;
  border-radius: 2rem;
  z-index: 1000;
  position: relative;
  margin-top: 80px;
}
.modalPop button {
    background-color: #4f179b;
    position: absolute;
    margin: auto;
    right: 0;
    margin-right: 2rem;
    margin-top: 3rem;
    background-color: #BB24EC;
}
.modalPop button:hover {
  background-color: #BB24EC;
  opacity: .5;
}
.modalPop input{
  color: #BB24EC;
}
.modalPop .shinyButtonSecondary{
  position: relative;
}
button.confirmAmount {
    position: relative;
}
.logoHeader{
  height: 60px;
  margin-left: auto;
  margin-right: auto
}
.sc-evZas.bltKoS {
  color: #9D94AB;
}
.makeStyles-footer-10{
  height: auto;
  display: flex;
}
footer{
  background-color: #4f179b;
}
.colFooter_2{
  text-align: center;
}
footer a{
  padding: .3rem;
}
.copyright{
  font-size: 1.2em;
  text-align: center;
}
@media screen and (min-width:576px){
  .colFooter_2{
    text-align: right;
  }
  .copyright{
    text-align: left;
  }
  button.shinyButtonSecondary, a.shinyButtonSecondary {
    margin-bottom: 0;
  }
}
@media screen and (min-width:960px){
  button.buttonDropDown{
    padding-left: inherit;
  }
}